import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const qdsLogin = resolve => require(['@/views/login/login'], resolve)     //qds-登录
const qdsRegister = resolve => require(['@/views/login/register'], resolve)     //qds-注册
const qdsForgetPassword = resolve => require(['@/views/login/forgetPassword'], resolve)     //qds-忘记密码
const qdsSearchResume = resolve => require(['@/views/resume/searchResume'], resolve)     //qds-简历模块
const deliverRecoredDetial = resolve => require(['@/views/resume/deliverRecoredDetial'],resolve) // qds-投递记录详情页
const myResumeDetial = resolve => require(['@/views/resume/myResumeDetial'],resolve) // 我的简历详情页
const qdsCustomerManager = resolve => require(['@/views/customer/customerManager'], resolve)     //qds-客户模块
const qdsNewCustomer = resolve => require(['@/views/customer/newCustomer'], resolve)     //qds-客户模块-新增客户
const qdsViewCustomer = resolve => require(['@/views/customer/viewCustomer'], resolve)     //qds-客户模块-查看客户详情
const qdsReleaseNewPost = resolve => require(['@/views/position/releaseNewPost'], resolve)     //qds-职位模块-发布职位
const qdsPostList = resolve => require(['@/views/position/index'], resolve)     //qds-职位模块-职位列表
const postDetails = resolve => require(['@/views/position/postDetails'], resolve)     //qds-职位模块-职位详细
const qdsPersonal = resolve => require(['@/views/personal/personal'], resolve)     //qds-个人中心模块
const qdsAttestation = resolve => require(['@/views/personal/attestation'], resolve)     //qds-个人中心模块-企业认证
const qdsChangePhone = resolve => require(['@/views/personal/changePhone'], resolve)     //qds-个人中心模块-换绑手机号
const qdsChangePassword = resolve => require(['@/views/personal/changePassword'], resolve)     //qds-个人中心模块-修改密码
const qdsAdvisorySetup = resolve => require(['@/views/personal/advisorySetup'], resolve)     //qds-个人中心模块-咨询设置
const qdsCompanyInfor = resolve => require(['@/views/personal/companyInfor'], resolve)     //qds-个人中心模块-公司信息
const qdsBasicInfor = resolve => require(['@/views/personal/basicInfor'], resolve)     //qds-个人中心模块-基础信息

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'qdsLogin',
      meta: {
        title: '登录'
      },
      component: qdsLogin
    },{
      path: '/qdsRegister',
      name: 'qdsRegister',
      meta: {
        title: '注册'
      },
      component: qdsRegister
    },{
      path: '/qdsForgetPassword',
      name: 'qdsForgetPassword',
      meta: {
        title: '忘记密码'
      },
      component: qdsForgetPassword
    },{
      path: '/qdsSearchResume',
      name: 'qdsSearchResume',
      meta: {
        title: '人才管理',
        requireAuth: true,
      },
      component: qdsSearchResume
    },
    {
      path:'/qdsSearchResume/deliverRecoredDetial',
      name:'resumeDetial',
      meta: {
        title: '投递记录详情页',
        requireAuth: true,
      },
      component: deliverRecoredDetial,
    },
    {
      path:'/qdsSearchResume/myResumeDetial',
      name:'myResumeDetial',
      meta: {
        title: '我的简历详情页',
        requireAuth: true,
      },
      component:myResumeDetial,
    }, {
      path: '/qdsCustomerManager',
      name: 'qdsCustomerManager',
      meta: {
        title: '客户',
        requireAuth: true,
      },
      component: qdsCustomerManager
    },{
      path: '/qdsNewCustomer',
      name: 'qdsNewCustomer',
      meta: {
        title: '新增客户',
        requireAuth: true,
      },
      component: qdsNewCustomer
    },{
      path: '/qdsViewCustomer',
      name: 'qdsViewCustomer',
      meta: {
        title: '客户详情',
        requireAuth: true,
      },
      component: qdsViewCustomer
    },{
      path: '/qdsReleaseNewPost',
      name: 'qdsReleaseNewPost',
      meta: {
        title: '发布职位',
        requireAuth: true,
      },
      component: qdsReleaseNewPost
    },{
      path: '/qdsPostList',
      name: 'qdsPostList',
      meta: {
        title: '优蓝快招',
        requireAuth: true,
      },
      component: qdsPostList
    },{
      path: '/postDetails',
      name: 'postDetails',
      meta: {
        title: '职位详情',
        requireAuth: true,
      },
      component: postDetails
    },{
      path: '/qdsPersonal',
      name: 'qdsPersonal',
      meta: {
        title: '个人中心',
        requireAuth: true,
      },
      component: qdsPersonal
    },{
      path: '/qdsAttestation',
      name: 'qdsAttestation',
      meta: {
        title: '企业认证',
        requireAuth: true,
      },
      component: qdsAttestation
    },{
      path: '/qdsChangePhone',
      name: 'qdsChangePhone',
      meta: {
        title: '换绑手机号',
        requireAuth: true,
      },
      component: qdsChangePhone
    },{
      path: '/qdsChangePassword',
      name: 'qdsChangePassword',
      meta: {
        title: '修改密码',
        requireAuth: true,
      },
      component: qdsChangePassword
    },{
      path: '/qdsAdvisorySetup',
      name: 'qdsAdvisorySetup',
      meta: {
        title: '咨询设置',
        requireAuth: true,
      },
      component: qdsAdvisorySetup
    },{
      path: '/qdsCompanyInfor',
      name: 'qdsCompanyInfor',
      meta: {
        title: '公司信息'
      },
      component: qdsCompanyInfor
    },{
      path: '/qdsBasicInfor',
      name: 'qdsBasicInfor',
      meta: {
        title: '基本信息'
      },
      component: qdsBasicInfor
    },
  ]
})



