// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

Vue.use(ElementUI);
Vue.use(MintUI)

import VueRouter from 'vue-router'
Vue.use(VueRouter)

import BaiduMap from 'vue-baidu-map'            //百度地图引用
Vue.use(BaiduMap, {
  ak: '1jtvvshwlALgPCCRN43N2r6P'
})

// import VConsole from 'vconsole/dist/vconsole.min.js' //调试
// let vConsole = new VConsole()

import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);

import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
Vue.config.productionTip = false

// post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

import './js/rem'                     //rem

// 配置快招全局域名
import domain from '@/js/domain.js';
global.domain = domain;

//设置cookie
Vue.prototype.setCookie=function(cname, cvalue, exdays) {
  var d = new Date();
  d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
  var expires = "expires=" + d.toUTCString();
    if (exdays == -1) {
      expires = '';
    }
    document.cookie = cname + "=" + cvalue + "; " + expires;
}
import Util from '@/js/mUtils';
router.beforeEach((to, from, next) => {
  window.document.title = to.meta.title;
  if (to.matched.some(record  => record.meta.requireAuth)) {    //根据字段判断是否路由过滤
    var qdsMToken = Util.getCookie('qdsMToken')
    if (qdsMToken) {
      next();
    }else if(qdsMToken == null || qdsMToken == '') {  
      next({
        path: '/',
        query: {redirect: to.fullPath}          // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }   
  }else {
    next();
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
 if(response.data.code == 10011) {                        //渠道商响应拦截器
    document.cookie = "qdsMToken" + "=" + '' + ";path=/"; 
    router.push('/');
  }
  return response;
}, function (error) {
  // 对响应错误做点什么
  if (error.response) {
    // 请求已发出，但服务器响应的状态码不在 2xx 范围内
    if(error.response.status == 500) {
      
    }
    if(error.response.status == 404) {
      
    }
  }
});



